import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useLocation } from "react-router-dom";
import { useNavigate } from 'react-router-dom'
import { getReadMailData, getSubscribeData, Token } from "../../api/api";
import { isMobile } from "../../App";
import { sendEventToAmplitude } from "../../components/Amplitude";
import PageLoding from "../../components/PageLoding";
import { SubscribeNewsLetterDataType, Summary } from "../../components/Summary";
import { SummaryItem } from "../SubscribePage";

export interface SummaryNewsLetterDataType {
  id: number,
  s3_object_key: string,
  subject: string,
  read_link: string,
  summary_list: SummaryItem,
  newsletter_id: number,
  date: string,
  from_name: string,
  html_body: string,
  share_text?: string
}


const ReadPage = () => {
  const [readmaildata, setReadMailData] = useState<SummaryNewsLetterDataType[]>([]);
  const [newslettersubscribe, setNewsLettersubscribe] = useState<SubscribeNewsLetterDataType[]>([])
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const mail = searchParams.get("mail");
  const authToken = Token();
  const navigate = useNavigate();



  useEffect(() => {
    if (isMobile) {
      navigate(`/mobileread?mail=${mail}`);
    } 
  }, [isMobile]);

  const handleGetNewsLetterData = async () => {
    try {
      const responesSubscribe = await getSubscribeData("testapi/newsletter?&subscribe_status=subscribed&sort_type=recent")
      setNewsLettersubscribe(responesSubscribe.data)
    } catch (error) {
      console.log("Api   ")
    }
  }


  const handleGetData = async () => {
    try {
      const response = await getReadMailData(mail);
      setReadMailData([response.data]);
      sendEventToAmplitude("view article detail", { "article name": response.data.from_name, "post name": response.data.subject });
    } catch (error) {
      console.log("Api   ", error);
    }
  };

  useEffect(() => {
    handleGetData();
    handleGetNewsLetterData();
  }, [location]);

  return (
    <div className="bg-white">
      {authToken ? "" : (
      <div className="flex sticky top-0 z-1 bg-white items-center justify-between border-b p-4">
        <img className="h-6 md:w-[90px] md:mt-[10px] md:ml-[10px] md:h-[20px]"
          src="/images/MailpocketLogo.png"
          alt="Logo"
          onClick={() => (window.location.href = "/landingpage")}
        />
        <div className="flex md:flex-col md:items-end md:mr-2">
          <span className="text-base font-extrabold">  ,  3  ?</span>
          <Link className="font-extrabold text-base underline ml-2 text-customPurple" to="/landingpage"> </Link>
        </div>
        <Link className="font-extrabold mr-4 text-base text-customPurple" to="/sign-in"></Link>
      </div>)}
      <div className="flex flex-col items-center justify-center pb-[80px]">
        <Summary summaryNewsLetterData={readmaildata} newslettersubscribe={newslettersubscribe} />
        <div className="border-b w-[730px] mt-10">
          <p className="font-bold text-lg p-3 text-gray-500"></p>
        </div>
        <div className="mt-2">
          {readmaildata.map((data) => {
            return data.html_body !== null ? (
              <div dangerouslySetInnerHTML={{ __html: data.html_body }} />
            ) : (
              <PageLoding />
            );
          })}
        </div>
        <div className="flex justify-center items-center fixed bottom-0  h-[80px]  mt-10">
          <div className="w-[730px] h-[80px] border-t absolute top-0 bg-white z-10">
            <img className="w-[180px] absolute top-4 right-0 cursor-pointer" src="/images/MailpocketLogo.png" alt="MailpocketLogo" onClick={() => (window.location.href = "/landingpage")} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default ReadPage;





